                <p class="text-sm font-medium text-gray-900 truncate">{{ file.customName || file.name }}</p>
                <div v-if="file.description"
                  class="mt-2 text-xs text-gray-600 h-24 overflow-y-auto description-scroll pr-2">
                  {{ file.description }}
                </div>
                <p class="text-sm text-gray-500 mt-2">{{ formatFileSize(file.size) }}</p>
                <p class="text-xs text-gray-400 mt-1">{{ formatDate(file.uploadedAt) }}</p>
              </div>
              <div class="flex space-x-2">
                <button @click="editFile(index)" class="text-gray-400 hover:text-gray-600" title="–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å">
                  <svg class="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2"
                      d="M11 5H6a2 2 0 00-2 2v11a2 2 0 002 2h11a2 2 0 002-2v-5m-1.414-9.414a2 2 0 112.828 2.828L11.828 15H9v-2.828l8.586-8.586z" />
                  </svg>
                </button>
                <button @click="copyLink(file.url)" class="text-gray-400 hover:text-gray-600" title="–ö–æ–ø–∏—Ä–æ–≤–∞—Ç—å —Å—Å—ã–ª–∫—É">
                  <svg class="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2"
                      d="M8 5H6a2 2 0 00-2 2v12a2 2 0 002 2h10a2 2 0 002-2v-1M8 5a2 2 0 002 2h2a2 2 0 002-2M8 5a2 2 0 012-2h2a2 2 0 012 2m0 0h2a2 2 0 012 2v3m2 4H10m0 0l3-3m-3 3l3 3" />
                  </svg>
                </button>
              </div>
            </div>

            <div class="mt-auto pt-3 flex space-x-3">
              <a :href="file.url" target="_blank"
                class="flex-1 px-3 py-2 bg-blue-50 text-blue-600 hover:bg-blue-100 text-sm font-medium rounded text-center">
                –°–∫–∞—á–∞—Ç—å
              </a>
              <button @click="deleteFile(index)"
                class="flex-1 px-3 py-2 bg-red-50 text-red-600 hover:bg-red-100 text-sm font-medium rounded">
                –£–¥–∞–ª–∏—Ç—å
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- –ú–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ —Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏—è -->
    <div v-if="editingFile !== null"
      class="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
      <div class="bg-white rounded-xl shadow-lg w-full max-w-md">
        <div class="p-6">
          <h3 class="text-lg font-medium text-gray-900 mb-4">–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏–µ —Ñ–∞–π–ª–∞</h3>

          <div class="space-y-4">
            <div>
              <label class="block text-sm font-medium text-gray-700 mb-1">–ù–∞–∑–≤–∞–Ω–∏–µ</label>
              <input v-model="editingData.customName" type="text"
                class="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                placeholder="–í–≤–µ–¥–∏—Ç–µ –Ω–∞–∑–≤–∞–Ω–∏–µ —Ñ–∞–π–ª–∞" maxlength="100">
              <p class="text-xs text-gray-500 mt-1">{{ editingData.customName.length }}/100</p>
            </div>

            <div>
              <label class="block text-sm font-medium text-gray-700 mb-1">–û–ø–∏—Å–∞–Ω–∏–µ</label>
              <textarea v-model="editingData.description" rows="3"
                class="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                placeholder="–í–≤–µ–¥–∏—Ç–µ –æ–ø–∏—Å–∞–Ω–∏–µ —Ñ–∞–π–ª–∞" maxlength="500" @input="limitDescription"></textarea>
              <p class="text-xs text-gray-500 mt-1">{{ editingData.description.length }}/500</p>
            </div>
          </div>

          <div class="mt-6 flex justify-end space-x-3">
            <button @click="editingFile = null"
              class="px-4 py-2 text-sm font-medium text-gray-700 bg-gray-100 hover:bg-gray-200 rounded-md">
              –û—Ç–º–µ–Ω–∞
            </button>
            <button @click="saveFileEdit"
              class="px-4 py-2 text-sm font-medium text-white bg-blue-600 hover:bg-blue-700 rounded-md">
              –°–æ—Ö—Ä–∞–Ω–∏—Ç—å
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// –ò–∫–æ–Ω–∫–∏ —Ñ–∞–π–ª–æ–≤
const FileIcon = {
  template: `
    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
      <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z"/>
    </svg>
  `
}

const ImageIcon = {
  template: `
    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
      <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z"/>
    </svg>
  `
}

const VideoIcon = {
  template: `
    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
      <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15 10l4.553-2.276A1 1 0 0121 8.618v6.764a1 1 0 01-1.447.894L15 14M5 18h8a2 2 0 002-2V8a2 2 0 00-2-2H5a2 2 0 00-2 2v8a2 2 0 002 2z"/>
    </svg>
  `
}

const AudioIcon = {
  template: `
    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
      <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 19V6l12-3v13M9 19c0 1.105-1.343 2-3 2s-3-.895-3-2 1.343-2 3-2 3 .895 3 2zm12-3c0 1.105-1.343 2-3 2s-3-.895-3-2 1.343-2 3-2 3 .895 3 2zM9 10l12-3"/>
    </svg>
  `
}

const ArchiveIcon = {
  template: `
    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
      <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M5 8h14M5 8a2 2 0 110-4h14a2 2 0 110 4M5 8v10a2 2 0 002 2h10a2 2 0 002-2V8m-9 4h4"/>
    </svg>
  `
}

export default {
  components: { FileIcon, ImageIcon, VideoIcon, AudioIcon, ArchiveIcon },

  data() {
    return {
      file: null,
      previewUrl: null,
      progress: 0,
      uploading: false,
      uploadedFiles: [],
      dragOver: false,
      editingFile: null,
      activeAccordion: null,
      editingData: {
        customName: '',
        description: ''
      },
      fileMeta: {
        customName: '',
        description: ''

      }

    }
  },

  created() {
    // –ó–∞–≥—Ä—É–∑–∫–∞ —Å–æ—Ö—Ä–∞–Ω–µ–Ω–Ω—ã—Ö —Ñ–∞–π–ª–æ–≤ –∏–∑ localStorage
    const savedFiles = JSON.parse(localStorage.getItem('uploadedFiles')) || []
    this.uploadedFiles = savedFiles.map(file => ({
      ...file,
      uploadedAt: new Date(file.uploadedAt)
    }))
  },

  methods: {
    toggleAccordion(section) {
      this.activeAccordion = this.activeAccordion === section ? null : section
    },

    onFileChange(event) {
      const file = event.target.files[0]
      if (file) {
        this.file = file
        this.generatePreview(file)
      }
    },

    handleDragOver() {
      this.dragOver = true
    },

    handleDragLeave() {
      this.dragOver = false
    },

    handleDrop(event) {
      this.dragOver = false
      const files = event.dataTransfer.files
      if (files.length > 0) {
        this.file = files[0]
        this.generatePreview(files[0])
      }
    },

    generatePreview(file) {
      if (this.previewUrl) {
        URL.revokeObjectURL(this.previewUrl)
      }

      if (file && this.isImage(file)) {
        this.previewUrl = URL.createObjectURL(file)
      } else {
        this.previewUrl = null
      }
    },

    fileIcon(file) {
      if (!file) return FileIcon

      if (file.type) {
        if (file.type.startsWith('image/')) return ImageIcon
        if (file.type.startsWith('video/')) return VideoIcon
        if (file.type.startsWith('audio/')) return AudioIcon
        if (file.type.includes('zip') || file.type.includes('compressed')) return ArchiveIcon
      }

      if (file.name) {
        const extension = file.name.split('.').pop().toLowerCase()
        const imageExtensions = ['jpg', 'jpeg', 'png', 'gif', 'webp', 'svg']
        const videoExtensions = ['mp4', 'mov', 'avi', 'mkv']
        const audioExtensions = ['mp3', 'wav', 'ogg']
        const archiveExtensions = ['zip', 'rar', '7z', 'tar', 'gz']

        if (imageExtensions.includes(extension)) return ImageIcon
        if (videoExtensions.includes(extension)) return VideoIcon
        if (audioExtensions.includes(extension)) return AudioIcon
        if (archiveExtensions.includes(extension)) return ArchiveIcon
      }

      return FileIcon
    },

    isImage(file) {
      if (!file) return false

      if (file.type) {
        return file.type.startsWith('image/')
      }

      if (file.name) {
        const extension = file.name.split('.').pop().toLowerCase()
        const imageExtensions = ['jpg', 'jpeg', 'png', 'gif', 'webp', 'svg']
        return imageExtensions.includes(extension)
      }

      return false
    },

    formatFileSize(bytes) {
      if (bytes === 0) return '0 Bytes'
      const k = 1024
      const sizes = ['Bytes', 'KB', 'MB', 'GB']
      const i = Math.floor(Math.log(bytes) / Math.log(k))
      return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i]
    },

    formatDate(date) {
      if (!date) return ''
      const options = { year: 'numeric', month: 'short', day: 'numeric', hour: '2-digit', minute: '2-digit' }
      return new Date(date).toLocaleDateString('ru-RU', options)
    },

    handleImageError(e) {
      e.target.src = 'data:image/svg+xml;base64,'
    },

    async uploadFile() {
      if (!this.file || this.uploading) return

      this.uploading = true
      this.progress = 0

      const simulateProgress = () => {
        return new Promise((resolve) => {
          const interval = setInterval(() => {
            if (this.progress < 90) {
              this.progress += 10
            } else {
              clearInterval(interval)
              resolve()
            }
          }, 300)
        })
      }

      try {
        await simulateProgress()

        const formData = new FormData()
        formData.append('file', this.file)

        const xhr = new XMLHttpRequest()

        xhr.upload.addEventListener('progress', (event) => {
          if (event.lengthComputable) {
            this.progress = 90 + Math.round((event.loaded / event.total) * 10)
          }
        })

        const response = await new Promise((resolve, reject) => {
          xhr.onreadystatechange = () => {
            if (xhr.readyState === 4) {
              if (xhr.status === 200) {
                resolve(JSON.parse(xhr.responseText))
              } else {
                reject(new Error('–û—à–∏–±–∫–∞ –∑–∞–≥—Ä—É–∑–∫–∏'))
              }
            }
          }

          xhr.open('POST', 'http://localhost:3000/upload', true)
          xhr.send(formData)
        })

        this.progress = 100

        this.uploadedFiles.unshift({
          name: this.file.name,
          size: this.file.size,
          url: response.fileUrl,
          type: this.file.type,
          customName: this.fileMeta.customName,
          description: this.fileMeta.description,
          uploadedAt: new Date()
        })

        localStorage.setItem('uploadedFiles', JSON.stringify(this.uploadedFiles))

        await new Promise(resolve => setTimeout(resolve, 500))
        this.file = null
        this.previewUrl = null
        this.fileMeta = { customName: '', description: '' }
        this.progress = 0
      } catch (error) {
        console.error('Upload error:', error)
        alert('–û—à–∏–±–∫–∞ –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ —Ñ–∞–π–ª–∞')
        this.progress = 0
      } finally {
        this.uploading = false
        if (this.previewUrl) {
          URL.revokeObjectURL(this.previewUrl)
          this.previewUrl = null
        }
      }
    },

    deleteFile(index) {
      const file = this.uploadedFiles[index]
      if (file.url && file.url.startsWith('blob:')) {
        URL.revokeObjectURL(file.url)
      }
      this.uploadedFiles.splice(index, 1)
      localStorage.setItem('uploadedFiles', JSON.stringify(this.uploadedFiles))
    },

    clearAllFiles() {
      if (confirm('–í—ã —É–≤–µ—Ä–µ–Ω—ã, —á—Ç–æ —Ö–æ—Ç–∏—Ç–µ —É–¥–∞–ª–∏—Ç—å –≤—Å–µ —Ñ–∞–π–ª—ã?')) {
        this.uploadedFiles.forEach(file => {
          if (file.url && file.url.startsWith('blob:')) {
            URL.revokeObjectURL(file.url)
          }
        })
        this.uploadedFiles = []
        localStorage.removeItem('uploadedFiles')
      }
    },

    copyLink(url) {
      navigator.clipboard.writeText(url)
        .then(() => {
          alert('–°—Å—ã–ª–∫–∞ —Å–∫–æ–ø–∏—Ä–æ–≤–∞–Ω–∞ –≤ –±—É—Ñ–µ—Ä –æ–±–º–µ–Ω–∞')
        })
        .catch(err => {
          console.error('Failed to copy: ', err)
        })
    },

    editFile(index) {
      this.editingFile = index
      this.editingData = {
        customName: this.uploadedFiles[index].customName || '',
        description: this.uploadedFiles[index].description || ''
      }
    },

    saveFileEdit() {
      if (this.editingFile !== null) {
        this.uploadedFiles[this.editingFile] = {
          ...this.uploadedFiles[this.editingFile],
          customName: this.editingData.customName,
          description: this.editingData.description
        }

        localStorage.setItem('uploadedFiles', JSON.stringify(this.uploadedFiles))
        this.editingFile = null
      }
    },

    limitDescription() {
      if (this.editingData.description.length > 500) {
        this.editingData.description = this.editingData.description.substring(0, 500)
      }
    }
  },

  beforeUnmount() {
    if (this.previewUrl) {
      URL.revokeObjectURL(this.previewUrl)
    }
    this.uploadedFiles.forEach(file => {
      if (file.url && file.url.startsWith('blob:')) {
        URL.revokeObjectURL(file.url)
      }
    })
  }
}
</script>

<style>
.progress-bar-transition {
  transition: width 0.3s ease-out;
}

tbody tr {
  transition: background-color 0.2s;
}

tbody tr:hover {
  background-color: #f8fafc;
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.3s, transform 0.3s;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
  transform: translateY(10px);
}

.description-scroll {
  scrollbar-width: thin;
  scrollbar-color: #cbd5e0 #f1f5f9;
}

.description-scroll::-webkit-scrollbar {
  width: 4px;
}

.description-scroll::-webkit-scrollbar-track {
  background: #f1f5f9;
  border-radius: 2px;
}

.description-scroll::-webkit-scrollbar-thumb {
  background-color: #cbd5e0;
  border-radius: 2px;
}
</style>